.project-title {
  min-height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #fff;
  text-decoration: none;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 16px auto 48px auto;
  max-width: 800px;
  text-align: center;
  gap: 16px;
}

.activity-suggestion {
  color: #fff;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 1.2rem;
}

.avatar-container {
  align-self: center;
  justify-self: center;
}

.intro-container,
.intro-container > * {
  color: #53b3cb;
  white-space: pre-line;
}

.avatar-img {
  border-radius: 100%;
  width: 150px;
  border: 2px solid #53b3cb;
}

.project-poster {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 400px;
  max-width: 500px;
  height: 150px;
  margin: 32px auto;

  background-color: #53b3cb;
  border-radius: 2rem;
  padding: 0.4em;
}

.clickable {
  cursor: pointer;
}

@media (max-width: 650px) {
  .container {
    margin: 8px 8px;
  }

  .project-poster {
    width: 80%;
  }
}
